@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;400;600&display=swap");

/********** CSS Reset ********/
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}

a {
  text-decoration: none;
  font-family: var(--font-family);
  color: var(--primary-color);
  font-size: 1.5rem;
  font-weight: 400;
}
body {
  font-family: var(--font-family);
  width: 100vw;
  overflow: hidden;
}
body::-webkit-scrollbar {
  display: none;
}

.App {
  width: 93vw;
  margin: 2rem auto;
  overflow: hidden;
}

/********* Reset End **********/

/*********** VARIABLES ***********/
:root {
  --font-family: "Roboto", sans-serf;
  --secondary-family: "Roboto Slab", serif;
  --normal-font: 400;
  --bold-font: 700;
  --bolder-font: 900;
  --bg-color: #fafcfa;
  --primary-color: #1d3557;
  --secondary-color: #e76f51;
  --primary-shadow: #8b8eaf;
  --secondary-shadow: #a17a69;
  --bottom-margin: 0.5rem;
  --bottom-margin-2: 1rem;
  --padding: 1rem;
  --line-height: 1.7rem;
  --transition: 0.3s;
}

/*********** VARIABLES END ***********/
/* ---------------------------------KEYFRAMES----------------------------------------- */

@keyframes spining {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* -----------------------KEYFRAMES END--------------------------------------------------- */
/* Navbar */
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.8rem;
}

ul {
  display: flex;
  width: 200px;
}
li {
  padding: 0rem 1.9rem;
}

.media {
  display: flex;
}
.media a {
  padding: 0rem 0.5rem;
}
a {
  display: block;
  position: relative;
  padding: 0.2em 0;
}

a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: var(--secondary-color);
  opacity: 0;
  transition: opacity 300ms, transform 300ms;
}

li a::after {
  opacity: 1;
  transform: scale(0);
  transform-origin: center;
}

li a:hover::after,
li a:focus::after {
  transform: scale(1);
}
/* Navbar */

/* -------------------------------------------------------------------------- */

/* Header */

.title {
  font-family: var(--font-family);
  color: var(--primary-color);
  letter-spacing: 0.7rem;
  font-size: 2.5rem;
  line-height: 5rem;
  font-weight: var(--bolder-font);
}
h2 {
  font-family: var(--secondary-family);
  color: var(--primary-color);
  font-weight: var(--normal-font);
  letter-spacing: 0.5rem;
  font-size: 2.3rem;
  line-height: 5rem;
}
.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 600px;
  overflow: hidden;
}
.avatar {
  width: 380px;
  margin-bottom: 2rem;
}

.logo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, 50%);
  left: 50px;
  top: 0px;
}
.Gr {
  position: absolute;
  animation: spining 2s ease-in-out infinite alternate;
}
.Br {
  position: absolute;
  z-index: 999;
}
.avatarCont {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.avatar {
  position: absolute;
  top: 20px;
  right: 10px;
  width: 250px;
}

/* ----------------------- ABOUT--------------------------------------------------- */

.about {
  display: flex;
  align-items: center;
  height: 700px;
  background: url(../public/images/back.svg) no-repeat;
  background-size: cover;
  position: relative;

  overflow: hidden;
  width: 100%;
}

.hand {
  position: absolute;
  right: -10px;
  top: 50%;
  transform: translateY(70%);
  top: 0;
  width: 550px;
}
.about-desc {
  background-color: var(--primary-color);
  height: 500px;
  width: 380px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(15%, 0%);
  border-radius: 15px;
  padding: 45px;
}

h3 {
  color: var(--secondary-color);
  font-family: var(--secondary-family);
  font-weight: var(--normal-font);
  font-size: 2rem;
  letter-spacing: 0.4rem;
  padding: 0.5rem 0;
  text-align: center;
}
.about-desc p {
  color: var(--bg-color);
  font-size: 1.2rem;
  font-family: var(--font-family);
  line-height: 1.4rem;
  letter-spacing: 1px;
  padding: var(--padding);
  text-align: justify;
}

/* -------------------------SKILS------------------------------------------------- */

.skils--container {
  display: flex;

  padding: 2rem;
  margin: 1rem;
  justify-content: center;
  overflow: hidden;
}
.skil {
  letter-spacing: 7px;
  font-size: 2.5rem;
  display: inline-block;
  position: relative;
  top: 50px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.design-title h2 {
  font-size: 1.4rem;
  letter-spacing: 2px;
}
.my-skils h2 {
  font-size: 1.4rem;
  letter-spacing: 2px;
  padding-top: 10px;
}
.title-skil,
.design-title {
  text-align: center;
}
.my-skils {
  display: flex;
  background: url(../public/images/newwave.svg);
  flex-direction: column;
  justify-content: space-around;
  padding: 2rem;

  align-items: center;
  border: 3px solid var(--primary-color);
  border-radius: 20px;
  margin-right: 1rem;
}
.languages {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 25px;
  grid-row-gap: 45px;
}
.design {
  width: 460px;
  height: 500px;
  border: 3px solid var(--primary-color);
  display: flex;
  background: url(../public/images/newwave.svg);
  flex-direction: column;
  justify-content: space-around;
  padding: 2rem;
  align-items: center;
  margin-left: 1rem;
  border-radius: 20px;
}
.design-title p {
  font-size: 1.3rem;
  font-family: var(--primary-family);
  color: var(--primary-color);
}
.design-tools {
  display: flex;
  align-items: center;
}
.design-tools img {
  padding: 0rem 0.6rem;
}

.skil::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: var(--secondary-color);
  transition: opacity 300ms, transform 300ms;
  opacity: 1;
  transform: scale(0);
  transform-origin: center;
}
.skil:hover::after,
.skil:focus::after {
  transform: scale(1);
}

/* -----------------------------Skils End--------------------------------------------- */

/* -------------------------- Project ------------------------------------------------ */

.project-title {
  letter-spacing: 7px;
  font-size: 2.4rem;
  display: inline-block;
  color: white;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.project-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: var(--secondary-color);
  transition: opacity 300ms, transform 300ms;
  opacity: 1;
  transform: scale(0);
  transform-origin: center;
}
.project-title:hover::after,
.project-title:focus::after {
  transform: scale(1);
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  column-gap: 10px;
  row-gap: 15px;
}
.project-section {
  width: 100vw;
  display: flex;
  height: 100vh;
  justify-content: center;
  overflow: hidden;

  background-color: #1d3557;
}
.project-img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: pointer;
  opacity: 0.9;
}
figure img:hover {
  scale: 1.2;
  opacity: 1.1;
  transition: 0.5s ease-in-out;
}
figure {
  width: 430px;
  height: 270px;

  overflow: hidden;

  border-radius: 10px;
}
.project-container {
  padding: 1rem;
  position: absolute;
}
.project-container h3 {
  color: white;
  padding: 1.9rem 0rem;
}

.modal {
  position: absolute;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-image {
  display: block;
  position: relative;
  margin: 0 auto;
  max-width: 70%;
  max-height: 70%;
  padding-top: 6rem;

  scale: 1.4;
}

/* -------------------------CONTACT ------------------------------------------------- */

.contact--title {
  letter-spacing: 7px;
  font-size: 2.4rem;
  display: inline-block;
  color: var(--primary-color);
  position: relative;
}
.contact--title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: var(--secondary-color);
  transition: opacity 300ms, transform 300ms;
  opacity: 1;
  transform: scale(0);
  transform-origin: center;
}
.contact--title::after,
.contact--title:focus::after {
  transform: scale(1);
}
.contact-container {
  width: 100vw;
  display: flex;
  flex-direction: column;

  align-items: center;
  height: 80px;
}

.btn-cont {
  width: 470px;
  height: 600px;
  border-radius: 10px;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background: url(../public/images/gr.png) -125px 25px;
}
.box-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  height: 640px;
}

.btn-cont h2 {
  color: var(--bg-color);
  font-size: 2.6rem;
  font-family: var(--secondary-family);
  font-weight: var(--bolder-font);
}
.btn-cont p {
  font-size: 2rem;
  letter-spacing: 5px;
  text-align: center;
  color: var(--secondary-color);
}
.btn-contact {
  padding: 1rem 2.5rem;
  border: 3px solid white;
  border-radius: 10px;
  background-color: var(--primary-color);
}
.btn-contact:hover {
  background-color: var(--secondary-color);
  opacity: 0.9;
}

.btn-contact a {
  color: white;
  font-size: 1.7rem;
  font-family: var(--font-family);
  font-weight: 300;
}

.test-cont {
  width: 400px;
  height: 400px;
  display: flex;
}

.section {
  height: 100vh;
  width: 100vw;
}

.fp-right ul {
  display: flex;
  flex-direction: column;
  width: 50px;
}
.fp-watermark {
  display: none;
}

